@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: 'Open Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#f0f0f0;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body::-webkit-scrollbar {
  width: 0.4em;
  height: 0.4em;
  padding: 1px;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #dba800;
  border-radius: 8px;
  outline: 3px solid #fff;
}


.ContactLinks{
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 1.5em;
    gap: 1.5em;
}

.ContactLinks img{
    width: 24px;
}
.Nav{
    display: flex;
    align-items: center;
    padding: 30px;
    box-sizing: border-box;
    width: 100%;
}

.Nav .heading{
    flex-grow: 1;
    font-size: 2em;
    font-weight: bold;
    color:#343434;
    margin: 0;
    font-family: 'Josefin Sans';
}
.AboutMe{
    height: 100vh;
}

.Content{
    padding: 30px;
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.aboutMeText{
    margin-top: 0;
    padding-top: 0;
    font-size: 1.2em;
    line-height: 1.8em;
    font-family: 'Open Sans';
    color: #343434;
    display: inline;
}

.myImage{
    width: 400px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.ViewMyResume{
    background-color: #f5f5f5;
    border: none;
    padding: 12px 24px;
    font-size: 1.2em;
    border-radius: 25px;
    font-family: 'Open Sans';
    border:1px solid #04456C;
    color: #04456C;
    font-weight: bold;
    position:relative;
    box-shadow: inset 0px 0 0 0 #292929;
    transition: all 300ms ease-in-out;
    display: flex;
    align-items: center;
}

.ViewMyResume:hover{
    color:#f5f5f5;
    border:1px solid #292929;
    box-shadow: inset 200px 0 0 0 #292929;
}

a{
    text-decoration: none;
    color:#04456C;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.arrow{
    margin-left: 24px;
    width: 20px;
    height: 20px;
    padding: 0;
    background:url(/static/media/arrow_forward_blue_24dp.ae50cd37.svg);
}
.Hero{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.Container{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    justify-content: center;
    grid-gap: 3em;
    gap: 3em;
}

.title{
    font-size: 3em;
    font-family: 'Josefin Sans';
    color:#343434;
    text-align: center;
    font-weight: 200;
    margin: 0;
}

.subtitle{
    font-size: 1.3em;
    color: #343434;
    text-align: center;
    font-family: 'Open Sans';
}
.Skills{
    height: 100vh;
    width: 100%;
}

.SkillsContainer{
    padding: 30px;
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 300px));
    justify-content: center;
    grid-gap:30px;
    gap:30px;
    padding-bottom: 80px;
}
.SkillItem{
    flex-basis: 25%;
    background: #f0f0f0;
    border:1px solid #04456C;
    border-radius: 30px;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: all 200ms ease-in-out;
    grid-gap:16px;
    gap:16px;
}

.SkillItem:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    box-shadow: 0px 0px 20px #c2c2c2;
    background-color: hsl(203, 37%, 51%);
}

.SkillItem:hover p{
    color: #fff;
}

.SkillItem:hover img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.SkillItem:hover .imgContainer{
    background-color: #fdfdfd;
}
.SkillItem p{
    margin: 0;
    transition: all 200ms ease-in-out;
}

.skillDescription{
    margin:0;
    font-size: 14px;
    flex-grow: 1;
    padding: 0px 16px 0px 16px;
}

.skillTitle{
    font-family: 'Josefin Sans';
    font-size: 1.1em;
    font-weight: bold;
    margin: 0;
    padding: 0px 16px 0px 16px;
}

.skillExperience{
    font-size: 14px;
    font-weight: bold;
    color:#444;
    padding: 0px 16px 16px 16px;
}

.imgContainer{
    height:130px;
    padding: 30px 30px 16px 30px;
    transition: all 200ms ease-in-out;
}

.imgContainer img{
    width:100%;
    height: 100%;
    transition: all 200ms ease-in-out;
}
